import React from 'react'
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import projects from '../data/projects.json'
import ProjectCard from './ProjectCard'

const useStyles = makeStyles(theme => ({
  grid: {
    marginBottom: theme.spacing(3),
    '&:last-child': {
      marginBottom: 0,
    }
  },
  company: {
    marginBottom: theme.spacing(10),
    '&:last-child': {
      marginBottom: 0,
    }
  },
  contentSm: {
    marginBottom: theme.spacing(3)
  },
  contentLg: {
    display: 'flex',
    alignItems: 'baseline',
    marginBottom: theme.spacing(3)
  },
  title: {
    fontWeight: 700,
    marginRight: theme.spacing(2)
  }
}));

const ProjectGrid = function (props) {
  const theme = useTheme();
  const classes = useStyles()
  const matches = useMediaQuery(theme.breakpoints.up('sm'));

  let getProjects = (json) => {
    let { company, team } = JSON.parse(json)

    return projects.filter(project => {
      return project.show && project.company === company && project.team === team
    })
  }

  const getValue = (json, key) => {
    let object = JSON.parse(json)

    return object[key]
  }

  let companies = projects.filter(({ show }) => show)
    .map(({ company, team, logo }) => JSON.stringify({company, team, logo }))
    .reduce((memo, company) => memo.includes(company) ? memo : [...memo, company], [])

  return (
    <div className={classes.root}>
      <Container>
        {companies.map((company, index) => (
          <div className={classes.company} key={index}>
            <div className={matches ? classes.contentLg : classes.contentSm}>
              <Typography variant='h4' color="textPrimary" className={classes.title} gutterBottom>
                {getValue(company, 'company')}
              </Typography>

              <Typography variant='h5' color="textSecondary" className={classes.title}>
                {getValue(company, 'team')}
              </Typography>
            </div>

            <Grid container spacing={4} className={classes.grid}>

              {getProjects(company).map(({ name, description, background, technologies, url }, index) => (
                <Grid item sm={12} md={4} key={index}>
                  <ProjectCard
                    name={name}
                    description={description}
                    image={background}
                    chips={technologies}
                    url={url}
                  />
                </Grid>
              ))}

            </Grid>
          </div>
       ))}
      </Container>
    </div>
  )
}

export default ProjectGrid